import {UiPopup} from './../../UI/UiPopup';
import {
  FormControl,
  TextField,
  TextareaAutosize,
  FormGroup,
  FormControlLabel,
  Checkbox
} from '@mui/material';

import {TYPE_OBJECT} from './../../../constants';
import { Button } from '@mui/material';


export const FormTypeObject = ({isOpen, setIsOpen}) => {
  return (
    <UiPopup open={isOpen}
      setOpen={setIsOpen}>
      <FormControl>
        <TextField label="  " variant="outlined"/>
        <TextareaAutosize aria-label="minimum height"
          minRows={3}
          placeholder=""
          style={
            {
              width: "100%",
              height: "100px"
            }
          }/>
        <FormGroup> {
          TYPE_OBJECT.map(type => (
            <FormControlLabel control={<Checkbox/>}
              label={type}/>
          ))
        } </FormGroup>

        <Button variant="contained">   </Button>
      </FormControl>
    </UiPopup>
  )
}
